


.navbar {
  background-color: #fff;
  min-height: 64px;
  padding: 0.5rem 1rem;
  position: relative;
}

/* Center the brand logo text */
.navbar-brand {
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1.3rem;
  color: #000 !important;
  flex-grow: 1; /* Allow the title to grow and fill the available space */
  display: inline-block; /* To prevent collapsing */
  transition: margin-left 0.3s ease; /* Smooth transition when shifting */
  margin-left: 0; /* Default position (centered) */
}

/* Adjusting user info and logout area */
.d-flex {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: nowrap;
  justify-content: flex-end;
}

/* User info text */
.user-info {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100px;
  font-size: 0.9rem;
  color: #ccc;
}

/* Button for logout */
.navbar .btn {
  font-size: 0.85rem;
  padding: 4px 12px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  background-color: #007bff;
  color: #fff;
  border: none;
}

.navbar .btn:hover {
  background-color: #0056b3 !important;
}

/* Mobile responsiveness adjustments */
@media (max-width: 520px) {
  /* CoinTracker text should shift left when user info is overlapping */
  .navbar-brand {
    font-size: 1.1rem; /* Slightly smaller on mobile */
    text-align: left;  /* Align to the left when screen is small */
    margin-left: 10px; /* Add margin to the left for a little space */
  }

  /* Make user info visible on small screens */
  .navbar .btn {
    padding: 4px 10px;
    font-size: 0.8rem;
  }

  .user-info {
    display: inline-block !important; /* Make user info visible on small screens */
    font-size: 0.85rem;
  }

  /* Add this rule to shift CoinTracker completely left with a margin */
  .navbar-brand {
    position: absolute; /* Ensure text can move without overlapping */
    left: 0; /* Align left */
    right: auto;
    margin-left: 10px; /* Add the left margin for a little space */
  }
}



